import { CompoundInterestInputs } from './../compound-interest.bloc';
import { Rounding } from './../../../common/utils/Rounding';
export class InterestTableBloc {
  public calculate(compountInterestInput: CompoundInterestInputs) {
    const result:{period:number, amount:number, interest: number}[] = [];
    if (!!compountInterestInput) {
      const { period, compoundedPerPeriod, rate, initailAmount, monthlyAddition } = compountInterestInput;
      let currentAmount = initailAmount;
      for (let i = 0; i < period; i++) {
        let interestPerPeriod = 0;
        for(let j =0 ;j<compoundedPerPeriod; j++) {
          const interest = (currentAmount + interestPerPeriod) * (rate / compoundedPerPeriod);
          interestPerPeriod += interest;
        }
        result.push({ period: i, amount: Rounding.round(currentAmount), interest: Rounding.round(interestPerPeriod) });
        currentAmount = +currentAmount + interestPerPeriod + monthlyAddition;
      }
      result.push({ period, amount: Rounding.round(currentAmount), interest: undefined });
    }
    return result;
  }

}
