import { InterestTableBloc } from './interest-table.bloc';
import { CompoundInterestInputs } from './../compound-interest.bloc';
import { Component, OnInit, Input, ChangeDetectionStrategy, OnChanges, SimpleChanges } from '@angular/core';

@Component({
  standalone: false,
  selector: 'app-interest-table',
  templateUrl: './interest-table.component.html',
  styleUrls: ['./interest-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class InterestTableComponent implements OnInit, OnChanges {

  @Input()
  compountInterestInput: CompoundInterestInputs;

  private readonly bloc = new InterestTableBloc();

  initialized = false;
  result:{period:number, amount:number, interest: number}[]= [];

  constructor() { }

  ngOnInit(): void {
    this.result = this.bloc.calculate(this.compountInterestInput);
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.result = this.bloc.calculate(this.compountInterestInput);
  }

}
