import { Rounding } from './../../common/utils/Rounding';
import { Bloc } from './../../common/utils/Bloc';
export class CompoundInterestInputs {

  readonly initailAmount: number
  readonly rate: number
  readonly compoundedPerPeriod: number
  readonly period: number
  readonly monthlyAddition: number

  constructor(
    initailAmount: number,
    rate: number,
    compoundedPerPeriod: number,
    period: number,
    monthlyAddition: number = 0 ) {
      this.initailAmount = +initailAmount;
      this.rate= +rate;
      this.compoundedPerPeriod = +compoundedPerPeriod;
      this.period = +period;
      this.monthlyAddition = +monthlyAddition;
  }

}

export class CompoundInterestBloc extends Bloc {
  private _finalAmount:number;

  public get finalAmount() : number {
    return this._finalAmount;
  }

  calculateFinalAmount(coumpoundInterstParams: CompoundInterestInputs) {
    const {initailAmount, rate, compoundedPerPeriod, period, monthlyAddition} = coumpoundInterstParams;
    const i = rate/compoundedPerPeriod;
    // const discountRate = i / (1+i)
    const additionPerDiscountRate = monthlyAddition / i;
    const result = (initailAmount+(additionPerDiscountRate))
          * Math.pow(1+(rate/compoundedPerPeriod), compoundedPerPeriod * period)
          - additionPerDiscountRate;
    this._finalAmount = Rounding.round(result)
  }
}
