import { CompoundInterestInputs } from './../compound-interest.bloc';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  standalone: false,
  selector: 'app-expert-input',
  templateUrl: './expert-input.component.html',
  styleUrls: ['./expert-input.component.less']
})
export class ExpertInputComponent implements OnInit {

  @Output()
  inputComplete = new EventEmitter<CompoundInterestInputs>();

  constructor() { }

  initailAmount = 1000;
  interestRatePercent = 4;
  compoundedPerPeriod = 1;
  period = 10;
  contributionPerPeriod = 0;

  ngOnInit(): void {
  }

  calculate(formValue: {initailAmount: number,
     interestRatePercent: number,
     compoundedPerPeriod: number,
     period: number,
     contributionPerPeriod: number}) {
    const {compoundedPerPeriod, initailAmount, interestRatePercent, period, contributionPerPeriod} = formValue;
    const input = new CompoundInterestInputs(
      initailAmount,
      interestRatePercent/100,
      compoundedPerPeriod,
      period,
      contributionPerPeriod);
    this.inputComplete.emit(input);
  }

}
