import { CompoundInterestBloc, CompoundInterestInputs } from './compound-interest.bloc';
import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';

@Component({
  standalone: false,
  selector: 'app-compound-interest',
  templateUrl: './compound-interest.component.html',
  styleUrls: ['./compound-interest.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CompoundInterestComponent implements OnInit {
  private compoundInterestBloc = new CompoundInterestBloc();
  private _finalAmount: number;

  public get finalAmount() : number {
    return this._finalAmount;
  }

  public set finalAmount(v : number) {
    this._finalAmount = v;
  }

  input: CompoundInterestInputs;

  constructor() {}

  ngOnInit(): void {}

  calculate(inputs: CompoundInterestInputs) {
    this.compoundInterestBloc.calculateFinalAmount(inputs);
    this.finalAmount = this.compoundInterestBloc.finalAmount;
    this.input = inputs;
  }
}
